<style scoped>
.card-detail {
  margin: 20px;
  padding: 20px;
}
</style>
<template>
  <v-container>
    <v-toolbar flat>
      <v-toolbar-title>Cration du dispatch</v-toolbar-title>
      <v-divider class="mx-4" inset vertical></v-divider>
      <v-spacer></v-spacer>
    </v-toolbar>
    <v-card>
      <v-card-text>
        <v-container>
          <v-row>
            <v-col>
              <v-btn @click="generate" color="primary darken-2" large>
                <v-icon dark>mdi-cloud-upload</v-icon>
                Gnrer le dispatch</v-btn
              >
            </v-col>
            <v-col>
              <v-progress-circular
                v-if="generating"
                indeterminate
                color="red"
              ></v-progress-circular>
            </v-col>
          </v-row>
        </v-container>
      </v-card-text>
    </v-card>

    <v-toolbar flat>
      <v-toolbar-title>Chargement du dispatch</v-toolbar-title>
      <v-divider class="mx-4" inset vertical></v-divider>
      <v-spacer></v-spacer>
    </v-toolbar>
    <v-card>
      <v-card-text>
        <v-container>
          <v-row>
            <v-col>
              <v-file-input
                label="Charger le fichier"
                v-model="file"
                filled
                prepend-icon="mdi-file"
              ></v-file-input>
            </v-col>

            <v-col cols="12" sm="6" md="4">
              <v-btn color="success" dark> </v-btn>
            </v-col>
          </v-row>
        </v-container>
      </v-card-text>
    </v-card>
  </v-container>
</template>
<script>
import axios from "axios";
export default {
  name: "PhotoUploader",
  data() {
    return {
      generating: false,
      file: null,
    };
  },
  methods: {
    generate: function () {
      this.generating = true;
      const options = {
        headers: {
          "x-access-token": "Bearer " + localStorage.getItem("token"),
        },
      };
      axios
        .get("/database/", options)
        .then((resp) => {
          console.log("Generate dispatch");
          if (resp.success)
            alert(`Fichier ${resp.filename} gnr avec succs`);
          else alert("Le processus a echou");
          this.generating = false;
        })
        .catch((err) => {
          console.log("ERROR ", err.message);
        });
    },
  },

  created: function () {},
};
</script>